.signInButton {
  background-color: #E0E0E0;
  height: 36px;
  padding: 0 2.6rem;
  text-transform: uppercase;
  display: inline-block;
  margin: 6px 0;
  border: none;
  border-radius: 2px;
  cursor: pointer;
  text-align: center;
  line-height: 36px;
  vertical-align: middle;
  white-space: nowrap;
  font-size: 1.4rem;
  letter-spacing: .03rem;
  position: relative;
  overflow: hidden;
  min-width: 88px;
  font-weight: 400;
  -webkit-box-shadow: 0 0 2px rgba(0, 0, 0, 0.12), 0 2px 2px rgba(0, 0, 0, 0.2);
          box-shadow: 0 0 2px rgba(0, 0, 0, 0.12), 0 2px 2px rgba(0, 0, 0, 0.2);
  color: #F77D0F;
  -webkit-transition: padding 200ms ease-out, background-color 200ms ease-out, font-size 150ms ease-out, width 300ms 100ms ease-out, border-radius 200ms ease-out, -webkit-box-shadow 200ms ease-out;
  transition: padding 200ms ease-out, background-color 200ms ease-out, font-size 150ms ease-out, width 300ms 100ms ease-out, border-radius 200ms ease-out, -webkit-box-shadow 200ms ease-out;
  -o-transition: padding 200ms ease-out, background-color 200ms ease-out, font-size 150ms ease-out, width 300ms 100ms ease-out, border-radius 200ms ease-out, box-shadow 200ms ease-out;
  transition: padding 200ms ease-out, background-color 200ms ease-out, font-size 150ms ease-out, width 300ms 100ms ease-out, border-radius 200ms ease-out, box-shadow 200ms ease-out;
  transition: padding 200ms ease-out, background-color 200ms ease-out, font-size 150ms ease-out, width 300ms 100ms ease-out, border-radius 200ms ease-out, box-shadow 200ms ease-out, -webkit-box-shadow 200ms ease-out; }

.signInButton:hover {
  background-color: #E0E0E0; }

.signInButton:active {
  -webkit-box-shadow: 0 0 4px rgba(0, 0, 0, 0.12), 1px 3px 4px rgba(0, 0, 0, 0.2);
          box-shadow: 0 0 4px rgba(0, 0, 0, 0.12), 1px 3px 4px rgba(0, 0, 0, 0.2);
  background-color: #E0E0E0; }

.signInButton:focus {
  outline: none; }

.signInButton--loading {
  font-size: 0;
  pointer-events: none; }

.signInButton--loading:hover {
  cursor: default;
  background-color: #E0E0E0; }

.signInButton--success {
  font-size: 0;
  background-color: #26CD57;
  width: 36px;
  min-width: 36px;
  border-radius: 50%;
  pointer-events: none;
  padding: 0; }

.signInButton--success:hover {
  background-color: #26CD57;
  cursor: default; }

.signInButton--success:active {
  background-color: #26CD57; }

.signInButton--success:focus {
  background-color: #26CD57; }

.signInButton--checkMark {
  position: absolute;
  top: calc(50% - 1rem);
  left: 0;
  height: 100%;
  width: 100%; }

.signInButton--checkMark svg {
  width: 2rem;
  fill: #F0F0F0; }

.signInButton--spinner {
  position: absolute;
  left: calc(50% - 15px);
  top: calc(50% - 15px); }

.signInButton--spinner svg {
  height: 30px;
  width: 30px; }



