/**
 * Timings and Animation
 */
.signIn {
  position: absolute;
  overflow: hidden;
  height: 100%;
  width: 100%;
  margin: 0;
  padding: 0;
  background-color: #2F4666; }

.signIn__logo__container {
  position: relative;
  height: 140px;
  overflow: hidden; }

.signIn__container {
  position: absolute;
  width: 400px;
  top: 10.5vh;
  left: calc(50% - 200px); }

.signIn__form {
  margin-top: 1rem;
  position: relative;
  border-radius: 4px;
  -webkit-transition: opacity 330ms ease-out, -webkit-transform 330ms ease-out;
  transition: opacity 330ms ease-out, -webkit-transform 330ms ease-out;
  -o-transition: transform 330ms ease-out, opacity 330ms ease-out;
  transition: transform 330ms ease-out, opacity 330ms ease-out;
  transition: transform 330ms ease-out, opacity 330ms ease-out, -webkit-transform 330ms ease-out;
  background: transparent; }

.signin__form-padding {
  padding: .5rem 0; }

.signIn__form__body {
  padding: 2rem;
  background: transparent; }

.signIn__form__actions {
  padding: 0 2rem 2rem;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center; }

.signIn__form__actions__msg {
  -ms-flex-item-align: center;
      align-self: center;
  font-size: 1.3rem;
  padding: 0 1rem;
  color: #E65F00; }

.signInFormEnter-appear {
  opacity: .1;
  -webkit-transform: translateY(3rem);
      -ms-transform: translateY(3rem);
          transform: translateY(3rem); }
  .signInFormEnter-appear.signInFormEnter-appear-active {
    opacity: 1;
    -webkit-transform: translateY(0);
        -ms-transform: translateY(0);
            transform: translateY(0); }

.signIn__form__body__inputs {
  width: 360px;
  position: relative;
  border-width: 1px;
  border-style: solid;
  border-color: #E0E0E0;
  overflow: hidden; }



