/*
 * Ripple Effect
 */
.buttonRipple__container {
  position: absolute;
  top: 0;
  left: 0;
  display: block;
  z-index: 1000000;
  overflow: hidden;
  height: 100%;
  width: 100%; }

.buttonRipple__ripple {
  position: absolute;
  top: 0;
  left: 0;
  border-radius: 50%;
  opacity: 0;
  pointer-events: none;
  height: 240px;
  width: 240px;
  background-color: #FFFFFF;
  -webkit-transform: scale(0.0001, 0.0001);
      -ms-transform: scale(0.0001, 0.0001);
          transform: scale(0.0001, 0.0001); }

.buttonRipple__ripple--visible {
  opacity: 0.3; }

.buttonRipple__ripple--animating {
  -webkit-transform: none;
      -ms-transform: none;
          transform: none;
  -webkit-transition: width 300ms cubic-bezier(0, 0, 0.2, 1), height 300ms cubic-bezier(0, 0, 0.2, 1), opacity 300ms cubic-bezier(0, 0, 0.2, 1), -webkit-transform 300ms cubic-bezier(0, 0, 0.2, 1);
  transition: width 300ms cubic-bezier(0, 0, 0.2, 1), height 300ms cubic-bezier(0, 0, 0.2, 1), opacity 300ms cubic-bezier(0, 0, 0.2, 1), -webkit-transform 300ms cubic-bezier(0, 0, 0.2, 1);
  -o-transition: transform 300ms cubic-bezier(0, 0, 0.2, 1), width 300ms cubic-bezier(0, 0, 0.2, 1), height 300ms cubic-bezier(0, 0, 0.2, 1), opacity 300ms cubic-bezier(0, 0, 0.2, 1);
  transition: transform 300ms cubic-bezier(0, 0, 0.2, 1), width 300ms cubic-bezier(0, 0, 0.2, 1), height 300ms cubic-bezier(0, 0, 0.2, 1), opacity 300ms cubic-bezier(0, 0, 0.2, 1);
  transition: transform 300ms cubic-bezier(0, 0, 0.2, 1), width 300ms cubic-bezier(0, 0, 0.2, 1), height 300ms cubic-bezier(0, 0, 0.2, 1), opacity 300ms cubic-bezier(0, 0, 0.2, 1), -webkit-transform 300ms cubic-bezier(0, 0, 0.2, 1); }



