.progressBar {
  display: block;
  width: 100%;
  margin: 0 auto;
  position: relative;
  height: 4rem;
  max-height: 4rem;
  background: transparent;
  border-radius: 0.3rem; }

.progressBar__meter {
  display: block;
  width: 100%;
  left: 0%;
  height: 2.4rem;
  top: 1.2rem;
  position: relative;
  border-radius: 1rem;
  -webkit-box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.16), 0 2px 10px 0 rgba(0, 0, 0, 0.12);
          box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.16), 0 2px 10px 0 rgba(0, 0, 0, 0.12);
  background-color: #f0f0f0; }

/* Style the progress bar and animate */
.progressBar__meter__progress {
  display: inline-block;
  background: #74ea96;
  width: 0%;
  max-width: 96%;
  left: 2%;
  top: 20%;
  height: 60%;
  border-radius: 1rem;
  position: absolute;
  text-align: center;
  background-size: 25px 25px;
  background-image: -webkit-linear-gradient(315deg, rgba(255, 255, 255, 0.35) 25%, transparent 25%, transparent 50%, rgba(255, 255, 255, 0.35) 50%, rgba(255, 255, 255, 0.35) 75%, transparent 75%, transparent);
  background-image: -o-linear-gradient(315deg, rgba(255, 255, 255, 0.35) 25%, transparent 25%, transparent 50%, rgba(255, 255, 255, 0.35) 50%, rgba(255, 255, 255, 0.35) 75%, transparent 75%, transparent);
  background-image: linear-gradient(135deg, rgba(255, 255, 255, 0.35) 25%, transparent 25%, transparent 50%, rgba(255, 255, 255, 0.35) 50%, rgba(255, 255, 255, 0.35) 75%, transparent 75%, transparent);
  -webkit-animation: animate-bars 7s linear infinite;
          animation: animate-bars 7s linear infinite;
  -webkit-transition: width 330ms ease-in-out;
  -o-transition: width 330ms ease-in-out;
  transition: width 330ms ease-in-out; }

.progressBar__meter__label {
  font-size: 1.3rem;
  font-weight: 300;
  text-align: center;
  line-height: 2.5rem;
  width: 100%;
  z-index: 2;
  position: absolute; }

@-webkit-keyframes animate-bars {
  0% {
    background-position: 0px 0px; }
  100% {
    background-position: 100% 0px; } }

@keyframes animate-bars {
  0% {
    background-position: 0px 0px; }
  100% {
    background-position: 100% 0px; } }



