/**
 * Timings and Animation
 */
* {
  margin: 0;
  padding: 0; }

body,
html {
  height: 100%;
  margin: 0;
  padding: 0;
  font-family: 'Roboto', sans-serif;
  font-size: 10px;
  background-color: #EEEEEE;
  position: relative;
  color: rgba(0, 0, 0, 0.85); }

html {
  overflow: auto; }

body a {
  text-decoration: none;
  color: inherit; }

input :focus {
  outline: none; }

select :focus {
  outline: none; }

.root {
  position: relative;
  height: 100%;
  width: 100%; }

/*
** Fade in Transition
*/
.fadeIn-enter {
  opacity: .01; }
  .fadeIn-enter.fadeIn-enter-active {
    opacity: 1;
    -webkit-transition: opacity 200ms ease-in;
    -o-transition: opacity 200ms ease-in;
    transition: opacity 200ms ease-in; }

.fadeIn-leave {
  opacity: 1; }
  .fadeIn-leave.fadeIn-leave-active {
    opacity: .01;
    -webkit-transition: opacity 200ms ease-in;
    -o-transition: opacity 200ms ease-in;
    transition: opacity 200ms ease-in; }

.fadeIn-appear {
  opacity: .01; }

.fadeIn-appear.fadeIn-appear-active {
  opacity: 1;
  -webkit-transition: opacity 200ms ease-in;
  -o-transition: opacity 200ms ease-in;
  transition: opacity 200ms ease-in; }

/*
** Fly In Transition
*/
.flyIn-enter {
  opacity: .01;
  -webkit-transform: translateY(2rem);
      -ms-transform: translateY(2rem);
          transform: translateY(2rem); }

.flyIn-enter.flyIn-enter-active {
  opacity: 1;
  -webkit-transform: translateY(0);
      -ms-transform: translateY(0);
          transform: translateY(0);
  -webkit-transition: opacity 200ms ease-in, -webkit-transform 200ms ease-in;
  transition: opacity 200ms ease-in, -webkit-transform 200ms ease-in;
  -o-transition: opacity 200ms ease-in, transform 200ms ease-in;
  transition: opacity 200ms ease-in, transform 200ms ease-in;
  transition: opacity 200ms ease-in, transform 200ms ease-in, -webkit-transform 200ms ease-in; }

.flyIn-leave {
  opacity: 1;
  -webkit-transform: translateY(0);
      -ms-transform: translateY(0);
          transform: translateY(0); }

.flyIn-leave.flyIn-leave-active {
  opacity: .01;
  -webkit-transform: translateY(2rem);
      -ms-transform: translateY(2rem);
          transform: translateY(2rem);
  -webkit-transition: opacity 200ms ease-in, -webkit-transform 200ms ease-in;
  transition: opacity 200ms ease-in, -webkit-transform 200ms ease-in;
  -o-transition: opacity 200ms ease-in, transform 200ms ease-in;
  transition: opacity 200ms ease-in, transform 200ms ease-in;
  transition: opacity 200ms ease-in, transform 200ms ease-in, -webkit-transform 200ms ease-in; }

.flyIn-appear {
  opacity: .01; }

.flyIn-appear.flyIn-appear-active {
  opacity: 1;
  -webkit-transition: opacity 200ms ease-in;
  -o-transition: opacity 200ms ease-in;
  transition: opacity 200ms ease-in; }



